import { Component, OnInit } from '@angular/core';
import {ProjectService} from '../../services/project.service';
import {Project} from '../../project';

@Component({
  selector: 'add-task',
  templateUrl: './add-task.component.html',
  styleUrls: ['./add-task.component.css'],
  providers:[ProjectService]
})
export class AddTaskComponent implements OnInit {
    projects:Project[];
    project_name:string;
    new_parent_task:string;
    new_sub_task:string;
    new_priority:number;
    new_start_date:string;
    new_end_date:string;
    new_completion_status:boolean;

    constructor(private projectService:ProjectService){
    this.projectService.getProject().subscribe(projects=>{
        this.projects=projects;
    });

 }

  ngOnInit() {
  }
  addTask(event:any){
      event.preventDefault();

      var newTask={
        project_name:this.project_name,
        parent_task:this.new_parent_task,
        sub_task:this.new_sub_task,
        priority:this.new_priority,
        start_date:this.new_start_date,
        end_date:this.new_end_date,
        completion_status:this.new_completion_status
      }
      console.log(newTask);
      this.projectService.updProject(newTask).subscribe(data =>{
            this.new_parent_task = newTask.parent_task;
            this.new_sub_task = newTask.sub_task;
            this.new_priority = newTask.priority;
            this.new_start_date = newTask.start_date;
            this.new_end_date = newTask.end_date;
            this.new_completion_status = newTask.completion_status;    
      })  

  }

}
